html[data-theme='light'] {
    --primaryGreen: #C2DC55;
    --secondaryGreen: #58985F;
    --darkGreen: #285E4D;
    --teal: #6CD3C2;
    --purple: #9F87E8;
    --yellow: #FDD504;
    --peach: #FF8080;
    --black: #333333;
    --grey: #6C6C6C;
    --silver: #ABABAB;
    --subtle: #CCCCCC;
    --light: #E5E5E5;
    --lighter: #F5F5F5;
    --lightest: #FAFAFA;
    --white: #FFFFFF;
    --indianEquity: #294C88;
    --debt: #00A6A4;
    --internationalEquity: #F97B30;
    --gold: #FAAD3B;
    --cash: #74BA85;
    --highestRisk: #FFE7E5;
    --secondHeighestRisk: #FFF1DD;
    --moderateRisk: #FFFBD3;
    --conservativeRisk: #F0F6D5;
    --lowestRisk: #EDFAFF;

    --red: #EA2F2F;
    --lightGreenBackground: #F1FCF6;
}

html[data-theme='light'] {
    --primaryGreen: #C2DC55;
    --secondaryGreen: #58985F;
    --darkGreen: #285E4D;
    --teal: #6CD3C2;
    --purple: #9F87E8;
    --yellow: #FDD504;
    --peach: #FF8080;
    --black: #333333;
    --grey: #6C6C6C;
    --silver: #ABABAB;
    --subtle: #CCCCCC;
    --light: #E5E5E5;
    --lighter: #F5F5F5;
    --lightest: #FAFAFA;
    --white: #FFFFFF;
    --indianEquity: #294C88;
    --debt: #00A6A4;
    --internationalEquity: #F97B30;
    --gold: #FAAD3B;
    --cash: #74BA85;
    --highestRisk: #FFE7E5;
    --secondHeighestRisk: #FFF1DD;
    --moderateRisk: #FFFBD3;
    --conservativeRisk: #F0F6D5;
    --lowestRisk: #EDFAFF;

    --red: #EA2F2F;
    --lightGreenBackground: #F1FCF6;
}

* {
    font-family: 'DM Sans';
}

/* text styles */

.screenTitle {
    font-size: 18px;
    font-weight: 500;
    color: var(--black);
    line-height: 23.44px;
}

.header1dark {
    font-size: 16px;
    font-weight: 500;
    color: var(--black);
    line-height: 22.4px;
}

.header1light {
    font-size: 16px;
    font-weight: 500;
    color: var(--grey);
    line-height: 22.4px;
}

.header2dark {
    font-size: 14px;
    font-weight: 500;
    color: var(--black);
    line-height: 19.6px;
}

.header2light {
    font-size: 14px;
    font-weight: 500;
    color: var(--grey);
    line-height: 19.6px;
}

.header3dark {
    font-size: 12px;
    font-weight: 500;
    color: var(--black);
    line-height: 16.8px;
}

.header3light {
    font-size: 12px;
    font-weight: 500;
    color: var(--grey);
    line-height: 16.8px;
}

.bigTextEntered {
    font-size: 24px;
    font-weight: 500;
    color: var(--black);
    line-height: 33.6px;
}

.bigTextPrompted {
    font-size: 24px;
    font-weight: 500;
    color: var(--silver);
    line-height: 33.6px;
}

.bodyTextDark {
    font-size: 14px;
    font-weight: 400;
    color: var(--black);
    line-height: 19.6px;
}

.bodyTextLight {
    font-size: 14px;
    font-weight: 400;
    color: var(--grey);
    line-height: 19.6px;
}

.smallTextDark {
    font-size: 12px;
    font-weight: 400;
    color: var(--black);
    line-height: 16.8px;
}

.smallTextLight {
    font-size: 12px;
    font-weight: 400;
    color: var(--grey);
    line-height: 16.8px;
}

.smallestTextDark {
    font-size: 10px;
    font-weight: 400;
    color: var(--black);
    line-height: 14px;
}

.smallestTextLight {
    font-size: 10px;
    font-weight: 400;
    color: var(--grey);
    line-height: 14px;
}

.solidButton {
    font-size: 14px;
    font-weight: 500;
    color: var(--darkGreen);
    background-color: var(--primaryGreen);
    height: 44px;
    width: 100%;
    text-align: center;
    border: none;
    border-radius: 8px;
}

.outlineButton {
    font-size: 14px;
    font-weight: 500;
    color: var(--darkGreen);
    height: 44px;
    width: 100%;
    text-align: center;
    border: 1px solid var(--darkGreen);
    border-radius: 8px;
    background-color: transparent;
}

.solidCardButton {
    font-size: 14px;
    font-weight: 500;
    color: var(--darkGreen);
    background-color: var(--primaryGreen);
    height: 44px;
    text-align: center;
    border: none;
    border-radius: 6px;
    min-width: 70%;
    padding-left: 8px;
    padding-right: 8px;
}

.outlineCardButton {
    font-size: 14px;
    font-weight: 500;
    color: var(--secondaryGreen);
    height: 44px;
    text-align: center;
    border: 1px solid var(--secondaryGreen);
    border-radius: 6px;
    min-width: 70%;
    padding-left: 8px;
    padding-right: 8px;
    background-color: transparent;
}

.mainContainer {
    display: flex;
    flex-direction: column;
}

@media screen and (min-color-index:0) and(-webkit-min-device-pixel-ratio:0) {
    .mainContainer {
        min-height: 100vh;
    }
}

.dataContainer {
    display: flex;
    flex-direction: column;
    flex: 1;
    padding: 0% 4%;
    padding-bottom: 70px;
}

.login-header {
    height: 160px;
    padding-top: 48px;
}

.login-footer {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 6% 4%;
}

.underlineGreen {
    color: var(--secondaryGreen);
    border-bottom: 1px dotted var(--secondaryGreen);
}

.mainHeader {
    background-color: var(--lightest);
    border-bottom: 1px solid var(--light);
    padding: 4%;
    display: flex;
    align-items: center;
}

.greenChipHeader {
    color: var(--secondaryGreen);
    font-size: 12px;
    font-weight: 500;
    padding: 4px 8px;
    border: 1px solid var(--secondaryGreen);
    border-radius: 50px;
    line-height: 16.8px;
    background-color: transparent;
}


.bottom-nav {
    width: 100%;
    height: 70px;
    padding-bottom: 18px;
    padding-top: 12px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: fixed;
    bottom: 0;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.4);
    background: var(--lightest);
    z-index: 1;
}

.bn-tab {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: end;
    align-content: space-between;
}

.footerIcon {
    color: var(--silver);
}

.footerIconActive {
    color: var(--darkGreen);
}

.footerText {
    font-size: 10px;
    font-weight: 400;
    color: var(--grey);
}

.footerTextActive {
    font-size: 10px;
    font-weight: 700;
    color: var(--darkGreen);
}

.hiddden-scrollbar {
    overflow-x: auto;
    white-space: nowrap;
    -ms-overflow-style: none;
    /* IE and Edge */
    scrollbar-width: none;
    /* Firefox */
}

.hiddden-scrollbar::-webkit-scrollbar {
    display: none;
    /*chrome and Safari */
}

.lineClamp {
    text-overflow: ellipsis;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}

.searchBar {
    display: flex;
    align-items: center;
    margin: 4%;
    padding: 4px;
    border: 1px solid var(--light);
    border-radius: 8px;
    height: 40px;
}

.searchInput {
    font-size: 14px;
    font-weight: 400;
    color: var(--silver);
    border: none;
    outline: none;
    width: 100%;
    margin-top: 2px
}

.searchInput::placeholder {
    font-size: 14px;
    font-weight: 400;
    color: var(--silver);
}

.normalChip {
    margin-right: 8px !important;
    border: 1px solid var(--light) !important;
    border-radius: 4px !important;
    background-color: transparent !important;
    height: auto !important;
}

.normalChip .MuiChip-label {
    color: var(--grey) !important;
    font-size: 12px !important;
    line-height: 16.8px !important;
    font-weight: 400 !important;
    padding: 4px 8px !important;
}

.normalChip.selected {
    border: 1px solid var(--secondaryGreen) !important;
}

.normalChip.selected .MuiChip-label {
    color: var(--secondaryGreen) !important;
    font-weight: 500 !important;
}

.footer {
    width: 100%;
    height: 70px;
    display: flex;
    padding: 4%;
    align-items: center;
    position: fixed;
    bottom: 0;
    border-top: 1px solid var(--light);
    background-color: var(--lightest);
    z-index: 1;
}

.solidCircleNumber {
    width: 24px;
    height: 24px;
    background-color: var(--darkGreen);
    color: var(--primaryGreen);
    font-size: 14px;
    font-weight: 700;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.progress-bar-custom {
    height: 4px !important;
    border-radius: 0;
    background-color: transparent;
}

.progress-bar-custom .progress-bar {
    background-color: var(--secondaryGreen) !important;
}

/*--------------radio-button--------------*/

.radio-item {
    display: block;
    padding: 0;
}

.radio-item input[type='radio'] {
    display: none;
    position: absolute;
}

.radio-item .radio-label {
    display: flex;
}

.radio-item .radio-label:before {
    content: ' ';
    margin-right: 12px;
    width: 20px;
    height: 20px;
    border-radius: 11px;
    border: 0.5px solid var(--silver);
    background-color: transparent;
}

.radio-item input[type='radio']:checked+.radio-label:before {
    border: 0.5px solid var(--secondaryGreen);
    background-color: var(--secondaryGreen);
    box-shadow: inset 0 0 0 4px var(--light);
}

.radio-item input[type='radio']:focus+.radio-label:before {
    outline: none;
    border-color: var(--secondaryGreen);
}

.normalInput {
    outline: none;
    border: none;
    border-bottom: 1px solid var(--light);
    margin-top: 2%;
    margin-bottom: 4%;
    font-size: 24px;
    font-weight: 500;
    width: 100%;
    caret-color: var(--secondaryGreen);
}

.normalInput:focus {
    border-bottom: 1px solid var(--secondaryGreen);
}

.normalInput::placeholder {
    font-size: 24px;
    font-weight: 500;
    color: var(--silver);
}

.normalInput.rupee {
    background-image: url('/src/assets/SVG/rupee-symbol.svg');
    padding-left: 20px;
    background-size: 16px;
    background-repeat: no-repeat;
    background-position-y: 9px;
}

.normalInput:disabled {
    color: var(--black);
    background-color: transparent
}

.errorMsg {
    color: var(--peach);
    font-size: 12px;
    font-weight: 400;
}

.spinner-grow,
.spinner-border {
    color: var(--secondaryGreen);
}

/* -----------------bottom drawer---------------------- */

.bottom-drawer .modal-dialog {
    width: 100%;
    margin: 0;
    position: absolute;
    bottom: -100px;
    transition: transform 0.4s !important;
}

.modal.fade.show .modal-dialog {
    transform: translate(0, -100px) !important;
}

.bottom-drawer .modal-content {
    background-color: var(--lightest) !important;
}

.bottom-drawer input[type="radio"] {
    margin-right: 10px;
}

.bottom-drawer .modal-footer {
    display: flex;
    gap: 4%;
    padding: 4%;
    border-top: 1px solid var(--light);
    height: 70px;
}

.bottom-drawer .modal-footer button {
    margin: 0;
    flex: 1;
}

/* tabs */
ul.nav-tabs {
    display: flex;
    border-bottom: 1px solid var(--light) !important;
    overflow-x: auto;
    white-space: nowrap;
    -ms-overflow-style: none;
    /* IE and Edge */
    scrollbar-width: none;
    flex-wrap: nowrap;
    overflow-y: hidden;
    padding-left: 4%;
    padding-top: 4%;
}

ul.nav-tabs::-webkit-scrollbar {
    display: none;
    /*chrome and Safari */
}

li.nav-item {
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 0;
    margin-right: 32px;
}

.nav-tabs .nav-link {
    color: var(--grey);
    padding-left: 0;
    padding-right: 0;
    border: none
}

.nav-tabs .nav-link.active {
    color: var(--black) !important;
    font-weight:500 !important;
    background-color: transparent;
    border-bottom: 3px solid var(--black) !important;
    border-top: none !important;
    border-right: none !important;
    border-left: none !important;
}

.nav-tabs .nav-link:hover {
    text-decoration: none !important;
}

.greenTabs.nav-tabs .nav-link.active{
    color: var(--secondaryGreen) !important;
    border-bottom: 3px solid var(--secondaryGreen) !important;
}